.btn {
    cursor: pointer;
    margin: 0 5px 5px 0;
    border-radius: 0;
    transition: all 0.3s ease-in-out; // padding: 10px 12px;
}

.btn-lg {
    font-size: $font-larger;
}

.btn-default {
    border: 1px solid $colorWhite;
    background: none;
    color: $colorWhite;
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        border: 1px solid $colorWhite;
        background: $colorWhite;
        color: $colorMainFirst;
    }
}

.btn-getTicket {
    border: 3px solid #f3e38c;
    background: none;
    color: #f3e38c;
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        border: 1px solid #f3e38c;
        background: #f3e38c;
        color: $colorMainFirst;
    }
}

.btn-primary {
    border: 1px solid $colorMainFirst;
    background: none;
    color: $colorMainFirst;
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        border: 1px solid $colorMainFirst;
        background: $colorMainFirst;
        color: $colorWhite;
    }
}

.btn-warning {
    border: 1px solid $colorMainThird;
    background: none;
    color: $colorMainThird;
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        border: 1px solid $colorMainThird;
        background: $colorMainThird;
        color: $colorMainFirst;
    }
}

.btn-detail {
    font-size: $font-small;
    padding: 5px 10px;
    background-color: $colorMainSecond;
    border-radius: 5px;
    color: #fff;
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        background: $colorMainFirst;
        color: #fff;
    }
}

.btn-switch {
    border: 1px solid $colorMainSecond;
    background: none;
    color: $colorMainSecond;
    font-size: $font-large;
    padding: 0.5rem 4rem;
    text-shadow: 0px 0px 10px $colorMainfourth;
    @include pad-below() {
        padding: 0.5rem 3rem;
    }
    @include mobile() {
        padding: 0.5rem 2.5rem;
    }
    &:first-child {
        border-radius: 10px 0 0 10px;
        margin: 0;
    }
    &:last-child {
        border-radius: 0 10px 10px 0;
    }
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        border: 1px solid $colorMainSecond;
        background: $colorMainSecond;
        color: #fff;
    }
}

.btn-group-switch {
    margin: auto;
}

.switch {
    text-align: center;
    margin: 20px auto;
}

.btn-rightNow {
    font-size: 1.125rem;
    padding: 1.125rem 1rem;
    background: $colorMainFirst;
    background-image: linear-gradient(150deg, $colorMainFirst 70%, transparent 0), linear-gradient(150deg, $colorSubFirst 100%, transparent 0);
    border-radius: 5px;
    color: #fff;
    @include pad-below() {
        padding: 0.5rem .8rem;
        font-size: $font-normal;
    }
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        background: $colorSubFirst;
        color: #fff;
    }
}

.btn-ticketDetail {
    color: $colorMainSecond;
    border: 3px solid $colorMainSecond;
    border-radius: 10px;
    padding: 1.5rem 2rem;
    margin-right: 2rem;
    font-size: $font-larger;
    @include pad-below() {
        margin: 4rem auto 2rem;
        font-size: $font-large;
        width: 100%;
        padding: 1rem;
    }
    &.is-active,
    &:hover,
    &:focus,
    &:active {
        background: $colorMainSecond;
        color: #fff;
    }
}

.btn-booking {
    position: absolute;
    right: 0;
    bottom: 0;
    color: #fff;
    border-bottom-right-radius: 25px;
    padding:3rem .5rem .5rem 4em;
    transition: all 0.3s ease-in-out;
    background-image: 
    linear-gradient(150deg,#fff 0%, transparent 0), 
    linear-gradient(150deg, #fff 51%, transparent 52%),
    linear-gradient(150deg, #609890 50%, transparent 61%),
    linear-gradient(150deg, $colorMainSecond 100%, transparent 0);

}
.btn-getTicket-intable{

    background-color: #F1B861;
    border-radius: 5px;
    color: #5B8E54;
    padding: 5px 10px;
    font-size: $font-small;

}